// import iconSrc from '@/static/mbcImg/publish/purchase/delete.png';
import rTitle from '@/components/common/RHX/rTitle/rTitle';
export default {
	components: {
		rTitle
	},
	props: [
		'icon',
		'data',
		'dataList',
		'disabled',
		'iconSrc',
		'iconWidth',
		'iconHeight',
		'iconTAlign',
		'iconPadding',
		'isShowOne',
		'valueOne',
		'keyNameOne',
		'typeOne',
		'placeholderOne',
		'placeholderStyleOne',
		'placeholderClassOne',
		'focusOne',
		'cursorOne',
		'adjustPositionOne',
		'maxlengthOne',
		'tTAlignOne',
		'tWidthOne',
		'tHeightOne',
		'tPaddingOne',
		'tBGOne',
		'tBorderOne',
		'tBStyleOne'
	],
	data() {
		return {
			index: -1,
			datas: this.data
		};
	},
	created() {},
	methods: {
		tapIndex (e) {
			console.log(e, '用户修改的数据的index');
			this.index = e;
		},
		// 点击完成按钮时触发
		tabTitleItem(e) {
			console.log(e, 'input返回值');
			this.datas.map((item,index)=>{
				if(index === this.index) {
					item.title = e[0],
					item.content = e[1]
				}
			});
			console.log(this.datas);
			this.$emit('tap-IconInput',this.datas);
		},
	}
};
